import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import menu01 from "./menu01";
import menu02 from "./menu02";
import menu03 from "./menu03";
import menu04 from "./menu04";
import menu05 from "./menu05";
import menu06 from "./menu06";
import menu07 from "./menu07";
const routes = [
  {
    path: "/",
    name: "home", //컴포넌트에 대한 별명 잘안씀
    component: HomeView, //Home같은 경우는 처음부터 import로 로딩하는것이 좋다
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ "../views/AboutView.vue"), //import로 모든것을 다운받는게 아니라,사용자가 해당 경로로 이동하고자 할때 다운로드받음
    //Arrow function은 콜백용으로 많이 쓴다
  },
  ...menu01, //배열 요소값을 뿌린다
  ...menu02,
  ...menu03,
  ...menu04,
  ...menu05,
  ...menu06,
  ...menu07,
  
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL), //History정보를 이용해 이동하도록 해준다, Context에 맞춰 History내부 바꾸어 주어야한다
  routes,
});

export default router;
