import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";//index.js 생략가능,Module을 갖고 올때 Module이 실행이 되지만 딱 한번만 실행하고 공유해서 쓴다
import store from './store'
import axiosConfig from "./apis/axiosConfig";

createApp(App).use(store).use(router).mount("#app");//router는 index.js의 정보를 보고 App.vue의 routerview에 띄어준다
//Vuex로 인해 use(store)가 추가되었다
